<template>
  <h1></h1>
  <div class="row text-center">
    <div class="col">
      <i @click="prevYear" class="bi bi-chevron-double-left calendar-icons"></i>
      <i @click="prevMonth" class="bi bi-chevron-left calendar-icons"></i>
    </div>
    <div class="col">
      <h4>{{ monthStr[month] }} {{ year }}</h4>
    </div>
    <div class="col">
      <i @click="nextMonth" class="bi bi-chevron-right calendar-icons"></i>
      <i
        @click="nextYear"
        class="bi bi-chevron-double-right calendar-icons"
      ></i>
    </div>
  </div>
  <div class="row shadow-sm p-2 mb-2 border-end">
    <div class="col bg-light">
      
    </div>
    <div class="col bg-light">
      
    </div>
    <div class="col bg-light">
      
    </div>
    <div class="col bg-light">
      
    </div>
    <div class="col bg-light">
      
    </div>
    <div class="col bg-dangerous bg-gradient">
      
    </div>
    <div class="col bg-danger bg-gradient">
      
    </div>
  </div>

  <div v-for="(week, idx) in calendar" :key="idx" class="row row-cols-sm-7">
    <Day
      v-for="(day, id) in week"
      :key="id"
      :calendarDay="day"
      :month="month"
      :selectedDay="selectedDay"
      @daycomp-select="selectDay"
    ></Day>
  </div>
</template>
<script>
import Day from "./Day";
import Calendar from "../class-calendar";
import { getDataOfDateRange } from "../controllers/api";
export default {
  data() {
    return {
      classCalendar: null,
      calendar: null,
      selectedDay: null,
      today: null,
      year: 2021,
      month: 1,
      monthStr: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ]
    };
  },
  async created() {
    this.classCalendar = new Calendar();
    this.classCalendar.init();
    this.calendar = this.classCalendar.calendar;
    this.today = new Date();
    this.month = this.today.getMonth();
    this.year = this.today.getFullYear();
    await this.refreshData();
  },
  methods: {
    init() {},
    selectDay(day) {
      this.selectedDay = day;
    },
    async refreshData() {
      const start = this.classCalendar.getStart();
      const end = this.classCalendar.getEnd();
      console.log(this.calendar);
      try {
        const res = await getDataOfDateRange(start, end);
        console.log(res);
        this.addDataToCalendar(res);
      } catch (error) {
        console.log("Error", error.response);
      }
    },
    async nextMonth() {
      if (this.month === 11) {
        this.year++;
        this.month = 0;
      } else {
        this.month++;
      }
      this.calendar = this.classCalendar.setStart(
        this.month,
        this.year
      ).calendar;
      await this.refreshData();
    },
    async prevMonth() {
      if (this.month === 0) {
        this.year--;
        this.month = 11;
      } else {
        this.month--;
      }
      this.calendar = this.classCalendar.setStart(
        this.month,
        this.year
      ).calendar;
      await this.refreshData();
      console.log(this.calendar);
    },
    nextYear() {
      this.year++;
      this.calendar = this.classCalendar.setStart(
        this.month,
        this.year
      ).calendar;
    },
    prevYear() {
      this.year--;
      this.calendar = this.classCalendar.setStart(
        this.month,
        this.year
      ).calendar;
    },
    addDataToCalendar(dataArr) {
      dataArr.forEach(element => {
        this.calendar = this.calendar.map(week => {
          return week.map(day => {
            if (element.dateStr === day.dateStr) {
              Object.assign(day, {
                dayData: element.dayData,
                photos: element.photos
              });
            }
            return day;
          });
        });
      });
    }
  },
  components: { Day }
};
</script>
<style scoped>
.calendar-icons {
  font-size: 2rem;
}
.calendar-icons:hover {
  color: cornflowerblue;
}
.calendar-day-op {
  opacity: 0.2;
}
</style>
