function firstDay(month = null, year = null) {
  const monthNow = month !== null ? month : new Date().getMonth();
  const yearNow = year || new Date().getFullYear();
  const dayOfWeek = new Date(yearNow, monthNow).getUTCDay();
  const firstDayMonth = new Date(yearNow, monthNow, 1 - dayOfWeek);
  console.log(firstDayMonth.toLocaleDateString());
  return firstDayMonth;
}
export default class Calendar {
  constructor(month = null, year = null) {
    this.firstDayMonth = firstDay(month, year);
    this.calendar = null;
    this.today = new Date();
  }
  init() {
    this.calendar = this.generateMonth();
  }
  setStart(month, year) {
    console.log(month);
    this.firstDayMonth = firstDay(month, year);
    this.init();
    return this;
  }
  generateWeek(firstDayOfWeek) {
    //
    const dayNow = firstDayOfWeek.getDate();
    const weekArr = new Array(7).fill("").map((day, index) => {
      const year = firstDayOfWeek.getFullYear();
      const month = firstDayOfWeek.getMonth();
      const date = new Date(year, month, dayNow + index);
      const dateStr = date.toLocaleDateString();
      const today = dateStr === this.today.toLocaleDateString() ? true : false;
      return { date: date, today, dateStr };
    });
    return weekArr;
  }
  generateMonth(weekNumber = 5) {
    const monthArr = [];
    for (let i = 0; i <= weekNumber; i++) {
      const weekArr = this.generateWeek(this.firstDayMonth); // WeekArr
      monthArr.push(weekArr);
      this.firstDayMonth.setDate(weekArr[6].date.getDate() + 1);
      if (!(this.firstDayMonth.getMonth() - 1 === weekArr[6].date.getMonth())) {
        this.firstDayMonth.setMonth(weekArr[6].date.getMonth());
      }
      if (
        !(
          this.firstDayMonth.getFullYear() - 1 ===
          weekArr[6].date.getFullYear()
        )
      ) {
        this.firstDayMonth.setYear(weekArr[6].date.getFullYear());
      }
    }
    return monthArr;
  }
  toStringDate() {
    return this.calendar.map(week =>
      week.map(day => day.date.toLocaleDateString())
    );
  }
  getStart() {
    return this.calendar[0][0].date.toLocaleDateString();
  }
  getEnd() {
    const index = this.calendar.length - 1;
    const index2 = this.calendar[index].length - 1;
    return this.calendar[index][index2].date.toLocaleDateString();
  }
}
