<template>
  <div
    :class="{
      'border-primary': calendarDay === selectedDay,
      'border-danger': calendarDay.today,
      'calendar-day-op': calendarDay.date.getMonth() !== month
    }"
    @mouseover="dayHover(calendarDay)"
    @click="daySelect(calendarDay)"
    class="col text-left bg-light border calendar-row"
    style="height: 140px;"
  >
    <h6>{{ calendarDay.date.getDate() }}</h6>
    <ul v-if="calendarDay.dayData" class="list-group day-li-element">
      <li
        class="list-group-item list-group-item-action list-group-item-primary"
        style="padding: 0;"
      >
        : {{ calendarDay.dayData?.weight }} 
      </li>
      <li
        class="list-group-item list-group-item-action list-group-item-info"
        style="padding: 0;"
      >
        : {{ calendarDay.dayData?.height }} 
      </li>
    </ul>
    <ul v-else class="list-group">
      <li
        v-if="myAge >= 0"
        class="list-group-item list-group-item-action list-group-item-danger day-li-element"
        style="padding: 0;"
      >
         
      </li>
      <li
        v-else
        class="list-group-item list-group-item-action list-group-item-warning"
        style="padding: 0;"
      >
          )
      </li>
    </ul>
  </div>
  <teleport to="body">
    <div
      v-if="editorOpen"
      :class="{ show: editorOpen }"
      class="modal fade"
      id="exampleModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
      style="display: block; padding-right: 17px;"
    >
      <div
        class="modal-dialog modal-xl modal-dialog-centered modal-dialog-scrollable"
      >
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              {{ calendarDay.date.toLocaleDateString() }}  :
              {{ myAge }} 
            </h5>
            <button
              @click="closeModal"
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div v-if="edit" class="modal-body">
            <div class="form-floating mb-3">
              <input
                v-model="postData.height"
                type="height"
                class="form-control"
                id="floatingInput"
                placeholder="65"
              />
              <label for="floatingInput"></label>
            </div>
            <div class="form-floating">
              <input
                v-model="postData.weight"
                type="weight"
                class="form-control"
                id="floatingPassword"
                placeholder=""
              />
              <label for="floatingPassword"></label>
            </div>
            <div class="mb-3">
              <label for="formFileMultiple" class="form-label"></label>
              <input
                @change="fileChange($event)"
                class="form-control"
                type="file"
                id="formFileMultiple"
                multiple
              />
            </div>
            <div class="row row-cols-6">
              <div
                v-for="(photo, idx) in calendarDay.photos"
                :key="idx"
                class="col-sm-2"
              >
                <button
                  @click="removePhoto(photo)"
                  type="button"
                  class="btn-close"
                  aria-label="Close"
                ></button>
                <img
                  :src="apiUrl + photo.url"
                  class="img-thumbnail float-start"
                  alt="..."
                />
              </div>
            </div>
          </div>
          <div v-else class="modal-body">
            <div class="row">
              <div class="row">
                <ul class="list-group" style="padding-left: 12px;">
                  <li class="list-group-item list-group-item-success">
                    : {{ calendarDay.dayData?.height }} 
                  </li>
                  <li class="list-group-item list-group-item-warning">
                    : {{ calendarDay.dayData?.weight }} 
                  </li>
                </ul>
              </div>
              <div class="row">
                <div
                  v-if="calendarDay.photos?.length > 0"
                  id="carouselExampleControls"
                  class="carousel slide"
                  data-bs-ride="carousel"
                >
                  <div
                    v-for="(photo, idx) in calendarDay.photos"
                    :key="idx"
                    class="carousel-inner"
                  >
                    <div class="carousel-item active">
                      <img
                        :src="apiUrl + photo.url"
                        class="d-block w-100"
                        alt="..."
                      />
                    </div>
                  </div>
                  <button
                    class="carousel-control-prev"
                    type="button"
                    data-bs-target="#carouselExampleControls"
                    data-bs-slide="prev"
                  >
                    <span
                      class="carousel-control-prev-icon"
                      aria-hidden="true"
                    ></span>
                    <span class="visually-hidden">Previous</span>
                  </button>
                  <button
                    class="carousel-control-next"
                    type="button"
                    data-bs-target="#carouselExampleControls"
                    data-bs-slide="next"
                  >
                    <span
                      class="carousel-control-next-icon"
                      aria-hidden="true"
                    ></span>
                    <span class="visually-hidden">Next</span>
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              @click="closeModal"
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              
            </button>
            <button
              v-if="!edit"
              @click="edit = true"
              type="button"
              class="btn btn-primary"
            >
              
            </button>
            <button
              v-else
              @click="saveDayChanges"
              type="button"
              class="btn btn-primary"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </teleport>
</template>
<script>
import {
  postDay,
  postPhoto,
  putDay,
  removePhotoOnServer
} from "../controllers/api";
export default {
  name: "Day",
  data() {
    return {
      apiUrl: process.env.VUE_APP_API,
      editorOpen: false,
      edit: false,
      slide: 1,
      postData: {
        weight: "",
        height: ""
      },
      photoFiles: []
    };
  },
  computed: {
    myAge() {
      const time = this.calendarDay?.date - new Date(2021, 0, 22);
      const age = time / 86400000;
      return age;
    }
  },
  methods: {
    async saveDayChanges() {
      const dateStr = this.calendarDay.date.toLocaleDateString();
      try {
        const res = this.calendarDay.dayData
          ? await putDay(dateStr, this.postData)
          : await postDay(dateStr, this.postData);
        this.edit = false;
        const { dayData, photos } = res;
        Object.assign(this.calendarDay, { dayData, photos });
      } catch (error) {
        console.log(error.response);
      }
    },
    async fileChange(evt) {
      this.photoFiles = evt.target.files;
      const formData = new FormData();
      const dateStr = this.calendarDay.date.toLocaleDateString();
      formData.append("dateStr", dateStr);
      this.photoFiles.forEach(file => formData.append("photos", file));
      try {
        const { dayData, photos } = await postPhoto(formData);
        Object.assign(this.calendarDay, { dayData, photos });
      } catch (err) {
        console.log(err.response);
      }
    },
    async removePhoto(photo) {
      const dateStr = this.calendarDay.date.toLocaleDateString();
      try {
        const { dayData, photos } = await removePhotoOnServer(dateStr, photo);
        Object.assign(this.calendarDay, { dayData, photos });
      } catch (err) {
        console.log("Delete error: ", err);
      }
    },
    dayHover(day) {
      this.$emit("daycomp-select", day);
    },
    daySelect(day) {
      this.$emit("daycomp-select", day);
      if (!this.calendarDay.dayData) {
        this.edit = true;
      }
      this.editorOpen = true;
      Object.assign(this.postData, this.calendarDay.dayData);
    },
    closeModal() {
      this.edit = false;
      this.editorOpen = false;
    }
  },
  props: {
    calendarDay: Object,
    month: Number,
    selectedDay: Object
  },
  emits: ["daycomp-select"]
};
</script>
<style scoped>
.calendar-row {
  padding-right: 0px !important;
  padding-left: 0px;
  overflow: hidden;
}
.calendar-day-op {
  opacity: 0.2;
}
.day-li-element {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
