import axios from "axios";
//const baseUrl = "http://localhost:3000/api";
const baseUrl = process.env.VUE_APP_API + "/api";
const client = axios.create({
  headers: { "Content-Type": "application/json" }
});

async function getOneDay(dayDate) {
  // DateStr
  const req = await client.get(`${baseUrl}/oneday?date=${dayDate}`);
  console.log(req);
}
async function getDataOfDateRange(start, end) {
  // Obj {start: DateStr, end: DateStr}
  const req = await client.get(`${baseUrl}/dayrange?start=${start}&end=${end}`);
  return req?.data;
}
async function postDay(dateStr, payload) {
  const req = await client.post(`${baseUrl}/addday`, {
    dateStr,
    data: payload
  });
  return req?.data;
}
async function putDay(dateStr, payload) {
  const req = await client.put(`${baseUrl}/addday`, {
    dateStr,
    data: payload
  });
  console.log(req);
  return req?.data;
}
async function postPhoto(formData) {
  const req = await client.post(`${baseUrl}/photos`, formData);
  return req?.data;
}
async function removePhotoOnServer(dateStr, photo) {
  const req = await client.delete(`${baseUrl}/photos`, {
    data: { dateStr, photo }
  });

  return req?.data;
}
export {
  getOneDay,
  getDataOfDateRange,
  postDay,
  postPhoto,
  putDay,
  removePhotoOnServer
};
